<template>
	<div class="box">
		<div class="row-2">
			<div class="col-4">
				<div id="list-example" class="list-group">
					<div class="list-group-item list-group-item-action des" v-for="item in CodeList.CodeList.CodeList" :key="item.id"
							@click="onclick(item.id)">
						第{{ item.id }}题
					</div>
				</div>
			</div>
		</div>
		<div class="code">
			<div class="msg">参考代码如下</div>
			<highlightjs language="cpp" :code="code" class="codetext"/>
		</div>
	</div>
</template>


<script setup>
import { defineProps, ref } from "vue"
import $ from 'jquery'
const CodeList = defineProps({
	CodeList: Object,
	m: String,
});
let code = ref("请选择题目")
function onclick(id) {
	console.log(id);
	$.ajax({
		type: "GET",
		url: "/"+ CodeList.CodeList.m + "/" + id + ".txt",
		dataType: "text",
		success: function (res) {
			console.log(res);
			code.value = res;
		}
	});
	
}
</script>

<style scoped>
	#list-example{
		width: 100px;
	}
	.col-4{
		width: 100px;
		float: left;
		height: 100%;
	}
	.code{
		width: calc(100% - 200px);
		height: 90%;
		margin-top: 10px;
		background-color: #1e1e1e;
		float: left;
		margin-left: 50px;
	}
	.codetext{
		box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
	}

	.box{
		height: 100%;
	}
	.msg{
		color: aliceblue;
		font-size: 20px;
	}
	.des{
		cursor: pointer;
	}

</style>
<style>
	body{
		width: 100%;
		background-color: #1e1e1e;
	}
</style>